<template>
    <div>
        <v-header>
            <div slot="right" :class="['h-right',service.serviceId==0 ? 'opa-6' : '']" v-if="type > 0">
                <a @click="setSkill" class="mg-r10">确定</a></div>
        </v-header>

        <v-content :type="'none'" style="position: static;">
            <div class="category dis-flex">
                <ul class="left-menu flex-none">
                    <li v-for="item in parents" @click="getChildren(item.serviceId)"
                        :class="[item.serviceId == parent.serviceId ? 'active':'']"
                        v-show="(((type==3||type==4)&&item.serviceId!=99) || type<3) && (type==3 || (type!=3 && item.serviceId !=9900))">
                        <span class="img" style="background-image:url({{item.icon | img-host}})"></span>
                        {{item.name}}
                    </li>
                </ul>
                <ul class="right-menu flex-1">
                    <li v-link="{name:'service-list',query:{id:parent.serviceId,name:parent.name}}"
                        v-show="parent.serviceId != 99 && type == 0">全部
                    </li>
                    <li v-for="item in children" @click="choose(item.name,item.serviceId)"
                        :class="[(type > 0 && item.serviceId == service.serviceId) ? 'color-main checked' :'']">
                        {{item.name}}
                        <span class="img choose"></span>
                    </li>
                </ul>
            </div>
        </v-content>
    </div>
</template>
<script>
    import VHeader from '../components/Header'
    import VContent from '../components/Content'

    import {url, st, publish} from '../const'
    export default{
        route: {
            data({from, to}){
                if (this.isReady) {
                    return {
                        state: !this.state
                    }
                }
            }
        },
        ready(){
            this.loadQuery();
            this.$http.get(url.allCategory).then(
                function (response) {
                    let data = response.data.data;

                    // let serviceId = 0;
                    // let query = this.$route.query;
                    // if(query.type && query.type == 3 && query.serviceId){
                    //    serviceId = query.serviceId;
                    // }

                    // if(query.type && query.type == 3){//增加通用选项
                    this.parents.push({
                        name: '通用',
                        serviceId: 9900,
                        icon: '/img/category/9900.png'
                    });
                    this.category.push({
                        name: '通用',
                        serviceId: 9900,
                        subCategorys: [{name: '通用', serviceId: 9901}]
                    });
                    // }
                    //填充所有父服务
                    for (let i in data) {
                        // //技能认证不需要显示热门行业
                        // if (data[i].serviceId == 99 && this.type == 4){
                        //   continue;
                        // }

                        if (!data[i].icon) {
                            data[i].icon = '/menu.png';
                        }
                        this.parents.push({
                            name: data[i].name,
                            serviceId: data[i].serviceId,
                            icon: data[i].icon
                        });
                        //加载所有服务
                        this.category.push({
                            name: data[i].name,
                            serviceId: data[i].serviceId,
                            subCategorys: data[i].subCategorys
                        });
                        // if(data[i].serviceId == serviceId){
                        //   this.service = {name:data[i].name,serviceId:serviceId};
                        // }
                    }
                    this.initChoose();
                }
            )
            this.isReady = true;
        },
        data(){
            return {
                isReady: false,
                state: false,

                parent: {},
                parents: [],
                children: [],
                category: [],
                type: 0,// 1、新建 2、修改 3、图片类别 4、技能认证
                service: {name: '', serviceId: 0}
            }
        },
        methods: {
            loadQuery(){
                let query = this.$route.query;
                if (query.type && query.type > 0) {
                    this.type = query.type;
                    if (query.type < 3) {
                        let p = publish.get(this.type);
                        if (!p) {
                            window.location.replace('#/publish');
                            return;
                        }
                        this.service = p.service;
                    } else if (query.type == 3) {
                        let service = st.getJson(st.keys.skillShow);
                        if (!service) {
                            window.location.replace('#/show/add');
                            return;
                        }
                        this.service = service;
                    } else if (query.type == 4) {
                        let p = st.getJson(st.keys.certificationEdit);
                        if (!p) {
                            window.location.replace('#/certificate/index');
                            return;
                        }
                        this.service.name = p.categoryName;
                        this.service.serviceId = p.category;
                    }
                }
            },
            initChoose(){
                if (this.type == 0 || !this.service.serviceId) {//选择
                    let ind = this.type == 3 ? 0 : this.type == 4 ? 2 : 1;
                    //选中第一个父服务
                    this.parent = this.parents[ind];
                    //赋值第一个children
                    this.children = this.category[ind].subCategorys;
                } else {//赋值
                    this.getChildren(this.service.serviceId);
                }
            },
            getChildren(id){
                id = parseInt(id);
                let str = id + "";
                if (id != 99 && str.substring((str.length - 2), str.length) != "00") {
                    id = parseInt(id / 100) * 100;
                }
                if (id === 0) {//未选择时
                    id = 99;
                }

                for (let i = 0; i < this.category.length; i++) {
                    if (this.category[i].serviceId == id) {
                        this.parent = this.parents[i];
                        this.children = this.category[i].subCategorys;
                        return;
                    }
                }
            },
            choose(name, id){
                if (this.type == 0) {
                    window.location.href = `#/service/list?id=${id}&name=${name}`;
                } else {
                    this.service = {
                        serviceId: id,
                        name: name
                    }
                }
            },
            setSkill(){
                if (this.type == 3) {
                    let service = this.service;
                    let str = service.serviceId + '';
                    if (str.substring(str.length - 2, str.length) == "99") {
                        service.name = this.parent.name + "-其他";
                    }
                    st.set(st.keys.skillShow, service);
                } else if (this.type == 4) {
                    let p = st.getJson(st.keys.certificationEdit);
                    p.category = this.service.serviceId;
                    let str = p.category + "";
                    if (str.substring(str.length - 2, str.length) == "99") {
                        p.categoryName = this.parent.name + "-其他";
                    } else {
                        p.categoryName = this.service.name
                    }
                    st.set(st.keys.certificationEdit, p);
                } else {
                    let p = publish.get(this.type);
                    p.service = this.service;
                    let str = p.service.serviceId + "";
                    if (str.substring(str.length - 2, str.length) == "99") {
                        p.service.name = this.parent.name + "-其他";
                    }
                    p.name = this.service.name;
                    publish.set(p, this.type);
                }
                window.history.back();
            }
        },
        watch: {
            'state': function () {
                this.type = 0;
                this.loadQuery();
                this.initChoose();
            }
        },
        components: {VHeader, VContent}
    }
</script>
<style scoped>
    .category {
        width: 100%;
        text-align: center;
        position: absolute;
        bottom: 0;
        top: .44rem;
    }

    .c-alone .category {
        top: .64rem;
    }

    .category ul {
        height: 100%;
        overflow: scroll;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
    }

    .left-menu {
        width: 1.2rem;
        padding-top: .1rem;
        background-color: #f4f4f4;
    }

    .left-menu li {
        height: .4rem;
        line-height: .4rem;
        font-size: .16rem;
        width: 100%;
        text-indent: .15rem;
        position: relative;
        padding-left: .1rem;
    }

    .left-menu li.active {
        background-color: #fff;
    }

    .left-menu li .img {
        width: .25rem;
        height: .25rem;
        background-size: .25rem;
        margin: .075rem auto;
        border-radius: 50%;
        position: absolute;
    }

    .right-menu {
        background-color: #fff;
        padding: .1rem;
    }

    .right-menu li {
        float: left;
        width: 40%;
        height: .35rem;
        line-height: .35rem;
        font-size: .14rem;
        background-color: #f0fafa;
        margin: .05rem 5% 5%;
        position: relative;
        border-radius: 0.05rem;
    }

    .right-menu .img.choose {
        position: absolute;
        bottom: 0;
        right: 0;
        width: .15rem;
        height: .15rem;
        background-image: url(../assets/img/icon/choose.png);
        background-size: .15rem;
        display: none;
    }

    .checked .img.choose {
        display: block;
    }
</style>



